<template>
  <div>
    <data-import :active="active" :data-list="dataList" />
    <div class="content">
      <el-row style="display: flex;">
        <el-col :span="18" class="importContent">
          <div v-show="active === 0" class="pt-20 alignCenter">
            <el-upload
              class="upload-demo" drag action="" :on-preview="handlePreview" :on-change="verifyExcel"
              :on-remove="handleRemove" name="file" :auto-upload="false" :limit="1"
            >
              <img :src="uploadImg" class="upload-img">
              <div class="el-upload__text">
                将文件拖到此处，或<em>点击上传</em>
              </div>
            </el-upload>
          </div>
          <div id="print" style="display: none;">
            <div v-for="(item, index) in excelPrintData" class="print-content" :key="index">
              <div v-for="(data, key) in item" :key="key" class="print-item">
                <span class="item-name">{{data.key}}</span>
                <span class="item-value">{{data.value}}</span>
              </div>
              <!-- <div style="display:flex;align-items:center;justify-content:center;">
                <vue-qr :text="getText(item)" :size="150"></vue-qr>
              </div> -->
            </div>
          </div>
          <div class="pt-20" style="margin-left: -10px">
            <el-button v-show="active === 0" type="primary" @click="print">
              打印
            </el-button>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<style scoped>
.upload-demo {
  width: 100%;
}
.upload-demo .upload-img {
  width: 64px;
  height: 81px;
  margin-bottom: 20px;
}
.upload-demo /deep/ .el-upload--text {
  width: 100%;
}
.upload-demo /deep/ .el-upload-dragger {
  width: 100%;
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.upload-demo /deep/ .el-upload-list {
  background-color: #FFEADF !important;
  line-height: 32px;
  margin-top: 10px;
}
.upload-demo /deep/ .el-upload-list .el-upload-list__item {
  padding: 10px;
  font-size: 16px;
}
.upload-demo /deep/ .el-upload-list .el-upload-list__item .el-icon-close {
  top: 16px;
  right: 10px;
}
.upload-demo /deep/ .el-icon-upload {
  margin-top: 80px;
}
</style>
<style scoped>
.content {
  margin: 0 20px;
  padding: 20px;
  background-color: #ffffff;
}
.importContent {
  margin: 20px auto;
}

  .desc {
    line-height: 32px;
  }
  .importImage {
    max-height: 90px;
  }
  .attention {
    line-height: 24px;
  }
  .lineHeight {
    line-height: 28px;
  }
  .pt-40 {
    padding-top: 40px;
  }
  .btnCancel {
    background-color: #cccccc;
    padding: 9px 15px;
    color: #ffffff;
  }
  .print-item{
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .item-name{
    font-size: 12px;
  }
  .item-value{
    font-size: 12px;
  }
  .print-content{
    margin-bottom: 20px;
  }
</style>

<script lang="ts">
import dataImport from '../ImportData/index.vue'
import { varifyExcel } from '../../utils/excelData'
// import vueQr from "vue-qr"
import uploadImg from '../../assets/icon-upload.png'

export default {
  components: {
    dataImport,
    // vueQr
  },
  props: [ ],
  data() {
    return {
      dataList: [
        {
          title: '数据导入'
        },
        {
          title: '文件导入'
        }
      ],
      active: 0,
      showResult: false,
      uploadImg: uploadImg,
      percentage: 0, // 导入进度
      importStatus: 0, // 文件导入状态，0还没开始导入，1导入成功，2导入失败
      importResult: [], // 导入结果
      varifyStatus: -1, // 文件校验状态 -1 校验中，1校验成功，0检验失败
      varifyResult: [], // 文件校验结果
      successNum: 0, // 导入成功条数
      failNum: 0, // 导入失败条数
      excelData: [], // 表格数据
      excelOriginData: [], // 原始表单数据
      excelPrintData: [] // excel打印数据
    }
  },
  mounted() {
  },
  methods: {
    getImportOptions() {
      return this.importOptions
    },
    handleFilter() {
      if (this.importOptions.isActivate === '1') {
        window.localStorage.setItem('apply_stadium', this.stadium_id)
      }
    },
    print() {
      //获取打印内容最外层dom节点
      let bdHtml = window.document.getElementById("print").innerHTML;
      //打印内容赋值innerHTML绘制新页面（window.print()打印当前页)
      window.document.body.innerHTML = bdHtml;
      //调用浏览器打印机
      window.print();
      //刷新页面返回当前页
      location.reload();
    },
    handleRemove() {
    },
    handlePreview() {},
    /**
     * 校验Excel文件，必填字段和模版正确性
     */
    verifyExcel(file) {
      this.showResult = true
      varifyExcel(file, this)
    },
    getText(data) {
      let text = ''
      data.forEach((item, index) => {
        if (index === 0) {
          text = text + item.key + ':' + item.value
        } else {
          text = text + '&' + item.key + ':' + item.value
        }
      })
      return text
    }
  }
}
</script>
